<template>
  <div class="today">
    <ul v-for="(item, index) in todayList" :key="index">
      <li v-for="(subItem, index) in item.list" :key="index">
        <p>{{subItem.name}}</p>
        <el-button type="primary" @click="toSubmit(item, subItem)"></el-button>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'app',
  data() {
    return {
      todayList: [{
        title: '',
        type: '',
        list: [{
          label: ''
        }]
      }, {
        title: '',
        type: '',
        list: [{
          label: ''
        }]
      }, {
        title: '',
        type: '',
        list: [{
          label: ''
        }]
      }]
    }
  },
  created() {
    this.initData()
  },
  methods: {
    toSubmit(item, subItem) {
      this.$router.push({name: 'submit', query: {id: subItem.id, title: item.title, type: item.type}})
    },
    initData() {
      this.$http.get('')
      .then((res) => {
        if (res.code === 200) {
          this.todayList = res.data
        } else {
          alert(res.msg)
        }
      })
      .catch((err) => {
        alert(err)
      })
    }
  },
  components: {
    
  }
}
</script>

