<template>
  <div class="list">
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="class" label="" :width="50">
      </el-table-column>
      <el-table-column prop="name" label="" :width="100">
      </el-table-column>
      <el-table-column prop="status" label="" :width="120">
      </el-table-column>
      <el-table-column prop="option" label="">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status === ''" @click="notify"></el-button>
          <el-button v-if="scope.row.status === ''" @click="toApproval(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="notify">
      <el-button type="primary" @click="notifyAll"></el-button>
    </div>
  </div>
</template>

<style scoped>
  .notify {
    margin-top: 40px;
    text-align: center;
  }
</style>

<script>
  export default {
    name: 'login',
    data() {
      return {
        tableData: [{
          class: 1,
          name: '',
          status: ''
        }, {
          
          class: 2,
          name: '',
          status: ''
        }, {
          
          class: 1,
          name: '',
          status: ''
        }, {
          
          class: 1,
          name: '',
          status: ''
        }]
      }
    },
    created() {
      this.init();
    },
    methods: {
      notify(item) {
        let params = {
          list: [item.id]
        }
        this.$http.get('', {params: params})
        .then((res) => {
          if (res.code === 200) {
            alert('')
          } else {
            alert('')
          }
        })
        .catch((err) => {
          console.log(err)
        })
      },
      notifyAll() {
        let array = []
        this.tableData.map((item) => {
          if (item.status) {
            array.push(item.id)
          }
        })
        let params = {
          list: array
        }
        this.$http.get('', {params: params})
        .then((res) => {
          if (res.code === 200) {
            alert('')
          } else {
            alert('')
          }
        })
        .catch((err) => {
          console.log(err)
        })
      },
      init() {
        let url = ''
        if (this.$route.query.type === 'prod') {
          url = ''
        } else {
          url = ''
        }
        this.$http.get(url)
        .then((res) => {
          if (res.code === 200) {
            this.tableData = res.data
          }
        })
        .catch((err) => {
          console.log(err)
        })
      },
      toApproval(item) {
        this.$router.push({name: 'adminMonDetail', query: {userId: item.name}})
      }
    },
    components: {
  
    }
  }
</script>

